.aboutus-container{
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.1);
  box-shadow: rgba(230, 230, 230, 0.16) 0 10px 36px 0, rgba(255, 255, 255, 0.06) 0 0 0 1px;
  border-radius: 5px;
  padding: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;

  .header-side {
    width: 100%;
    height: 10%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-left: 30px;
    padding-right: 30px;
    border-bottom: 1px solid #01B3B0;

    .filter-box {
      align-items: center;
      display: flex;

      .inputs {
        select{
          width: 70px;
          margin-right: 20px;
          height: 28px;
          border-radius: 5px;
        }
      }
    }

    .main-price {
      font-weight: bold;
      color: white;
    }
  }
  .content-card {
    margin-top: 20px;
    width: 100%;
    height: 85%;
    display: flex;

    .left {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      width: 40%;
      height: 100%;
      padding: 20px;
      .inputs {
        width: 100%;
        height: 100%;
        overflow-y: auto;
        padding: 10px;
        input {
          margin-top: 10px;
          width: 100%;
          height: 35px;
          box-shadow: none;
          border: none !important;
          color: black;
        }

        textarea {
          margin-top: 15px;
          padding: 10px;
          width: 100%;
          height: 150px;

          &:focus {
            outline: none;
          }
        }

        label {
          width: 100%;
          margin-top: 15px;
          font-size: 16px;
          font-weight: bold;
          color: black;
        }

        .add-button {
          width: max-content;
          display: flex;
          justify-content: space-around;
          margin: 30px auto;
          font-weight: bold;
          cursor: pointer;
          padding: 5px 20px 5px 20px;
          background-color: white;
          border-radius: 5px;
          align-items: center;
          color: black;
        }
      }
    }

    .right {
      padding: 30px;
      width: 60%;
      height: 100%;
      overflow-y: auto;
      display: flex;
      justify-content: center;
      align-items: center;
      .cards {
        margin-top: 30px;
        width: 100%;
        height: 220px;
        background-color: rgba(255, 255, 255, 0.8);
        display: flex;
        align-items: center;
        box-shadow: rgb(92, 92, 92) 0px 0px 15px, rgb(99, 99, 99) 0px 0px 10px;
        border-radius: 5px;
        overflow: hidden;

        &:first-child {
          margin-top: 0;
        }

        .for-img {
          width: 40%;
          height: 100%;
          padding: 10px;

          img {
            width: 100%;
            height: 100%;
          }
        }

        .for-text {
          width: 60%;
          height: 100%;
          padding: 10px;
          .des {
            width: 100%;
            height: 100%;
            overflow-y: auto;
            padding: 10px;
          }
        }
      }
    }
  }
}